export default {
	data() {
		return {
			oldpassword: '',
			newpassword: '',
			renewpassword: '',
			disable: true,
			loading: false
		}
	},
	watch: {
		oldpassword(val) {
			this.change()
		},
		newpassword(val) {
			this.change()
		},
		renewpassword(val) {
			this.change()
		},
	},
	methods: {
		// 监听输入框 
		change() {
			if(this.oldpassword && this.newpassword && this.renewpassword) {
				this.disable = false
			} else {
				this.disable = true
			}
		},
		// 验证层
		check() {
			if(!this.oldpassword) {
				uni.showToast({
					title: '旧密码不能为空',
					icon: 'none'
				});
				return false
			}
			if(!this.newpassword) {
				uni.showToast({
					title: '新密码不能为空',
					icon: 'none'
				});
				return false
			}
			if(!this.renewpassword) {
				uni.showToast({
					title: '确认密码不能为空',
					icon: 'none'
				});
				return false
			}
			if(this.renewpassword !== this.newpassword) {
				uni.showToast({
					title: '两次输入的新密码不一致',
					icon: 'none'
				});
				return false
			}
			if(this.oldpassword && this.newpassword && this.renewpassword) {
				return true;
			}
			uni.showToast({
				title: '请将数据填写完整',
				icon: 'none'
			});
			return false;
		},
		// 提交
		submit() {
			this.loading = true;
			
			if(!this.check()) {
				this.loading = false;
				return false
			}
			this.loading = false; 
			this.disable = true;
			uni.showToast({
				title: '验证通过',
				icon: 'none'
			});
			this.disable = false;
		}
	}
}
